import React, { useState } from 'react';
import { Box, Button, Grid, Paper, Stack, Typography } from '@mui/material';
import { useDispatch, useSelector } from 'react-redux';
import { useParams } from 'react-router-dom';
import StarIcon from '@mui/icons-material/Star';
import StarOutlineIcon from '@mui/icons-material/StarOutline';
import StarHalfIcon from '@mui/icons-material/StarHalf';
import { addToCart, decreaseQuantity, increaseQuantity } from '../store/slices/CartSlice';
import Features from '../Components/Features';

const SingleProduct = () => {
    const { id } = useParams();
    const dispatch = useDispatch();
    const products = useSelector((state) => state.filters.items);
    const cartItems = useSelector((state) => state.carts.cartItems);
    const singleProduct = products.filter((item) => item._id == id);

    const { title, category, star, price, size, color, description, bullet_point, image } = singleProduct[0];
    

    const item = cartItems.filter((curItem) => curItem._id == singleProduct[0]._id);
    const itemExist = item.length > 0 ? true : false;

    const [mainImage, setMainImage] = useState(image[0]);

    const ratingStar = Array.from({ length: 5 }, (elem, index) => {
        let number = index + 0.5;
        return (
            <span key={index}>
                {
                    star >= index + 1 ? (
                        <StarIcon color='primary' />
                    ) : star >= number ? (
                        <StarHalfIcon color='primary' />
                    ) : (
                        <StarOutlineIcon color='primary' />
                    )
                }
            </span>
        )
    })
    return (
        <>
            <Typography variant='h5' textAlign='center' mt={2}>Product Details</Typography>
            <Grid container p={2}>
                <Grid item sm={12} md={6} lg={6} mb={3}>
                    <Grid container spacing={3}>
                        <Grid item xs={4} sm={3} md={3} lg={2}>
                            <Box display='flex' flexDirection='column' justifyContent='space-around' alignItems='center' height='100%'>
                                {
                                    image.map((curElm, index) => {
                                        return (
                                            <Button key={index} onClick={() => setMainImage(curElm)} sx={{ width: '60px' }}>
                                                <img src={curElm.url} alt={curElm.filename} height='60px' />
                                            </Button>
                                        )
                                    })
                                }
                            </Box>
                        </Grid>
                        <Grid item xs={8} sm={9} md={8} lg={8} >
                            <Box component='img' src={mainImage.url} width='100%' height='100%' sx={{ objectFit: 'cover' }} bgcolor='#fafafa'></Box>
                        </Grid>
                    </Grid>
                </Grid>
                <Grid item sm={12} md={6} lg={6}>
                    <Paper sx={{ padding: '1rem' }}>
                        <Typography variant='h6'>{title}</Typography>
                        <small>{"("}{bullet_point}{")"}</small><br /><br />
    
                        <Typography mb={2}>Price: Rs {price}/-</Typography>
                        <Typography mt={2} variant='h8'>rating:</Typography><br />
                        {ratingStar}
                    
                        <Features />

                        <Typography mt={2}>Features:</Typography>
                        <ul className='feature'>
                            <li>Care Instructions: Dry Clean Only</li>
                            <li>Size : {size} </li>
                            <li>Fabric : {bullet_point} </li>
                            <li>Color : {color} </li>
                            <li>Handcrafted by Rural traditional weavers</li>
                            <li>Skin and Environment friendly.</li>
                            <li>Can be worn casually as well as with formals. Pair it with your ethnic attire or style it along with western outfits. Pashwool shawls and stole are very versatile.</li>
                            <li>Easy to wear and pair with other clothes.</li>
                            <li>Its a perfect gift for your lover, family, friend and coworkers.</li>
                        </ul>

                        {
                            itemExist ?
                                <Box mt={4}>
                                    <Button variant='outlined' sx={{ minWidth: 50, padding: 1, marginRight: "10px" }} onClick={() =>  dispatch(increaseQuantity(id))}>+</Button>
                                    {item[0].cartQuantity}
                                    <Button variant='outlined' sx={{ minWidth: 50, padding: 1, marginLeft: "10px" }} onClick={() =>  dispatch(decreaseQuantity(id))}>-</Button>
                                </Box>
                                :
                                <Button variant='contained' sx={
                                    {
                                        backgroundColor: "#153a5b",
                                        marginTop: '1rem',
                                        '&:hover':
                                        {
                                            backgroundColor: '#234e8b'
                                        }
                                    }
                                }
                                    onClick={() => dispatch(addToCart(singleProduct[0]))}
                                >
                                    Add to cart
                                </Button>

                        }
                    </Paper>
                </Grid>
            </Grid>

            <Box mt={2} pl={3}>
                <Typography variant='h6' mb={2}>Additional Information</Typography>
                <Typography><b>Package Dimensions</b> : 23.1 x 20.7 x 3.6 cm</Typography>
                <Typography><b>Weight</b> : 140 Grams</Typography>
                <Typography><b>Manufacturer</b> : SWARN SUTRA</Typography>
                <Typography><b>ASIN</b> : B0BQ2CYQXJ</Typography>
                <Typography><b>Category</b> : {category}</Typography>
                <Typography><b>Net Quantity</b> : 1.00 count</Typography>
                <Typography><b>Generic Name</b> : Stole</Typography>
                <Typography><b>Country of Origin</b> : India</Typography>

            </Box>

            <Box p={2}>
                <Typography variant='h6' mb={2}>Product Description</Typography>
                <Typography>{description}</Typography>


            </Box>
        </>
    )

}

export default SingleProduct;