import React, { Fragment, useEffect, useState } from 'react'
import { Button, Tab, Tabs, Toolbar, Typography, Box, useTheme, useMediaQuery, IconButton, Tooltip, Avatar, Menu,MenuItem, Badge } from '@mui/material';
import AppBar from '@mui/material/AppBar';
import ShoppingCartIcon from '@mui/icons-material/ShoppingCart';
import DrawerComp from './DrawerComp';
import { Link, useNavigate} from 'react-router-dom';
import logo from '../images/swarnsutra_logo.jpg';
import { useDispatch, useSelector } from 'react-redux';
import { userLogOut } from '../store/slices/UserSlice';

const Navbar = () => {
    const Pages = ["home", "about", "products", "blog"];
    const settings = ['Profile', 'Orders', 'Logout'];
    const[value, setValue] = useState(0);
    const[open, setOpen] = useState(false);
    const cartItem = useSelector((state)=> state.carts.cartItems);
    const theme = useTheme();
    const isMatch = useMediaQuery(theme.breakpoints.down('md'));
    const navigate = useNavigate();
    const dispatch = useDispatch();
    const {isAuthenticated, userName} = useSelector((state)=>state.user);
   
    const logOut = (setting)=>{
      setOpen(false);
       if(setting == "Logout"){
          dispatch(userLogOut())
          localStorage.clear();
          navigate('/');
       }
       if(setting=="Profile"){
        navigate('/profile');
       }
      //  if(setting=="Orders"){
      //   navigate('/orderDetails');
      //  }

    }

  return (
    <Fragment>
      <AppBar sx={{background:"#fff", color:"black"}} position="sticky">
       
       <Toolbar>
        <Box component='div' textAlign="left" flex={1}>
           <Link to='/' onClick={(e)=>setValue(0)}>
           <Box component='img' src={logo} width={120} height={64} sx={{objectFit:"cover"}}/>
           </Link>
        </Box>
         {isMatch ? (
          <>
            <DrawerComp/>
          </>
         ):
         (
           <>
           <Tabs  textColor="inherit" sx={{flexGrow:"1"}} value={JSON.parse(sessionStorage.getItem("value"))==null? 0 :JSON.parse(sessionStorage.getItem("value"))} onChange={(e,value)=>sessionStorage.setItem("value", value)} indicatorColor="primary">
             {
               Pages.map((page, index)=>(
                 <Tab key={index} label={page} LinkComponent={Link} to={index > 0 ? `/${page}` : '/'}/>
               ))
             }
           </Tabs>
            
            <Button LinkComponent={Link} to='/cart'>
              <Badge badgeContent={cartItem.length} color="primary">
                <ShoppingCartIcon color='inherit'/>
              </Badge>
            </Button>
            
            
            {
              isAuthenticated == true?
               <Box sx={{ flexGrow: 0 }}>
               <Tooltip title={userName}>
                 <IconButton  sx={{ p: 0 }} onClick={()=>setOpen(!open)}>
                   <Avatar alt={userName} src="/static/images/avatar/2.jpg" />
                 </IconButton>
               </Tooltip>
               <Menu
                 sx={{ mt: '45px' }}
                 id="menu-appbar"
                 anchorOrigin={{
                   vertical: 'top',
                   horizontal: 'right',
                 }}
                 keepMounted
                 transformOrigin={{
                   vertical: 'top',
                   horizontal: 'right',
                 }}
                 open={open}
                 onClose={(e)=>setOpen(false)}
               >
                 {settings.map((setting) => (
                   <MenuItem key={setting}>
                     <Typography textAlign="center" onClick={()=> logOut(setting)}>{setting}</Typography>
                   </MenuItem>
                 ))}
               </Menu>
             </Box>
             :
             <Button LinkComponent={Link} to='/login' sx={{marginRight:"5px"}}>Login</Button>
            }
         
           </>
         )
         }
         </Toolbar>
       </AppBar>
    </Fragment>
  )
}

export default Navbar;
