import React, { useState } from 'react'
import { Avatar, Box, Button, Checkbox, Container, CssBaseline, FormControlLabel, Grid, TextField, Typography } from '@mui/material';
import LockOutlinedIcon from '@mui/icons-material/LockOutlined';
import { Link , useNavigate} from 'react-router-dom';
import { toast } from 'react-toastify';
import Loading from './Loading';
const Signup = () => {
  const [loading,setLoading]=useState(false);
  const navigate=useNavigate();
  const [name,setName]=useState(null);
  const [email,setEmail]=useState(null);
  const [password,setPassword]=useState(null);
  const data={name,email,password};
  function handleSubmit(e){
    setLoading(true);
    e.preventDefault();
    if(name && email && password){
      fetch("http://193.160.119.184:9236/signup",{
          method:'post',
          headers:{
              'Accept':'Application/json',
              'Content-Type':'Application/json'
          },
          body:JSON.stringify(data)
      }).then((result)=>{
         result.json().then((result2)=>{
          if(result2.status==223){
            setLoading(false)
            toast.warn(result2.message);
          }
          else if(result2.status==200){
            setLoading(false)
            toast.success("OTP sent successfully Please check your email inbox or spam folder");
            localStorage.setItem("Info", JSON.stringify(result2));
            navigate('/verify');
          }else{
            setLoading(false)
            toast.error(result2.message);
          }
        })
      })
    }else{
      setLoading(false)
      toast.warn("Please input all required filled");
    }
}

  return (
    <>
    {loading?<Loading/>:
    
    <Container component="main" maxWidth="xs" sx={{paddingBottom:"2rem",boxShadow:"rgb(32 32 32 / 8%) 0px 10px 32px 0px;",marginBottom:'2rem'}}>
        <CssBaseline />
        <Box
          sx={{
            marginTop: 5,
            display: 'flex',
            flexDirection: 'column',
            alignItems: 'center',
          }}
        >
          <Avatar sx={{ m: 1, bgcolor: 'secondary.main' }}>
            <LockOutlinedIcon />
          </Avatar>
          <Typography component="h1" variant="h5">
            Sign up
          </Typography>
          <Box component="form" noValidate onSubmit={handleSubmit} sx={{ mt: 3 }}>
            <Grid container spacing={2}>
              <Grid item xs={12} sm={12} md={12} lg={12} xl={12}>
                <TextField
                  autoComplete="given-name"
                  name="firstName"
                  required
                  fullWidth
                  id="firstName"
                  label="Full Name"
                  autoFocus
                  onChange={(e)=>setName(e.target.value)}
                />
              </Grid>
              <Grid item xs={12} sm={12} md={12} lg={12} xl={12}>
                <TextField
                  required
                  fullWidth
                  id="email"
                  label="Email Address"
                  name="email"
                  autoComplete="email"
                  onChange={(e)=>setEmail(e.target.value)}
                />
              </Grid>
              <Grid item xs={12} sm={12} md={12} lg={12} xl={12}>
                <TextField
                  required
                  fullWidth
                  name="password"
                  label="Password"
                  type="password"
                  id="password"
                  autoComplete="new-password"
                  onChange={(e)=>setPassword(e.target.value)}
                />
              </Grid>
            </Grid>
            <Button
              type="submit"
              fullWidth
              variant="contained"
              sx={{ mt: 3, mb: 2 }}
            >
              Generate Otp
            </Button>
            <Grid container justifyContent="flex-end">
              <Grid item>
              Already have an account?{" "}
                <Link to="/login" variant="body2">
                 { "Sign in"}
                </Link>
              </Grid>
            </Grid>
          </Box>
        </Box>
      </Container>
    }
  </>
  )
}

export default Signup;
