import {Grid, Stack} from "@mui/material";
import CardComponent from "../Components/CardComponent";
import content from '../blogContent';
  
const Blog=()=>{
    return(
        <>
        <Stack >
          {
            content.map((data)=>{
                return(
                    <Grid item lg={4} md={4} sm={6} xs={12} textAlign="center" key={data.id}>
                        <CardComponent id={data.id} image={data.image} title={data.title} desc={data.desc} imageText={data.imageText} linkText={data.linkText}/>
                    </Grid>
                )
              })

          }
        </Stack>
        </>
    )
}

export default Blog;