import React, { Fragment } from "react";
import { Box, Button, Container, Grid, Stack, Typography } from "@mui/material";
import HourglassBottomIcon from '@mui/icons-material/HourglassBottom';
import { useSelector } from "react-redux";
import { Link } from "react-router-dom";

const OrderDetails = () => {
    const cartProducts = useSelector((state) => state.carts);
    const { cartItems } = cartProducts;
    const {userName, userEmail, shippingDetails} = useSelector((state) => state.user);
    const {address, city, country, pincode} = shippingDetails;

    const subtotal = cartItems.reduce((acc, item)=>acc + item.cartQuantity * item.price, 0);
    const list = ["Product", "Price", "Quantity", "Total"];
    return (
        <Container sx={{ padding: '2rem' }}>
            <Grid container>
                <Grid item xs={12} sm={12} md={12} lg={12}>
                    <Typography variant="h6">Shipping Address:</Typography>
                    <Box display='flex' alignItems='center' gap='0.4rem' mt={2}>
                        <Typography>Name:</Typography>
                        <span style={{ fontSize: '0.9rem', color: 'gray' }}>{userName}</span>
                    </Box>
                    <Box display='flex' alignItems='center' gap='0.4rem' mt={1}>
                        <Typography>Address:</Typography>
                        <span style={{ fontSize: '0.9rem', color: 'gray' }}>{address}, {city}, {country}-{pincode}</span>
                    </Box>
                </Grid>
                <Grid item xs={10} sm={12} md={12} lg={12} mt={3}>
                    <Typography variant="h6">Order Summary:</Typography>
                    <Box display='flex' alignItems='center' gap='0.4rem' mt={2}>
                        <Typography>Order Placed:</Typography>
                        <span style={{ color: 'green', fontSize: '1rem' }}>5 March 2023</span>
                    </Box>
                    <Box display='flex' alignItems='center' gap='0.4rem' mt={1}>
                        <Typography>Order ID:</Typography>
                        <span style={{ color: 'green', fontSize: '1rem' }}>OD1201002</span>
                    </Box>
                    <Box display='flex' alignItems='center' gap='0.4rem' mt={1}>
                        <Typography>Payment Mode:</Typography>
                        <span style={{ color: 'green', fontSize: '1rem' }}>Cash on Delivery</span>
                    </Box>
                    <Box display='flex' alignItems='center' gap='0.4rem' mt={1}>
                        <Typography>Order Status:</Typography>
                        <HourglassBottomIcon color='primary' /><span style={{ color: 'green' }}> Processing</span>
                    </Box>
                    <Box display='flex' alignItems='center' gap='0.4rem' mt={1}>
                        <Typography>Amount:</Typography>
                        <span style={{ fontSize: '1rem', color: 'green' }}>{subtotal}/-</span>
                    </Box>
                </Grid>
                <Grid item xs={12} sm={12} md={12} lg={12} mt={3}>
                    <Typography variant="h6">Your Order Items:</Typography>
                    <Grid container>

                        {
                            list.map((item, index) => (
                                <Grid item key={index} xs={3} sm={3} md={3} lg={3} mt={2} mb={2}>
                                    <Typography textAlign={index>0 ? 'end' : 'start'}>{item}</Typography>
                                </Grid>
                            ))
                        }
                        {
                            cartItems.map((item) => (
                                <Fragment key={item._id}>
                                    <Grid item xs={3} sm={3} md={3} lg={3} mt={2}>
                                        <img src={item.url} width="100" style={{ objectFit: "contain" }} />
                                        <Typography>{"("}{item.title}{")"}</Typography>
                                    </Grid>
                                    <Grid item xs={3} sm={3} md={3} lg={3} textAlign='end'>
                                        <Typography>{item.price}</Typography>
                                    </Grid>
                                    <Grid item xs={3} sm={3} md={3} lg={3}  textAlign='end' pr={3}>
                                        <Typography>{item.cartQuantity}</Typography>
                                    </Grid>
                                    <Grid item xs={3} sm={3} md={3} lg={3}  textAlign='end' >
                                        <Typography>{item.cartQuantity * item.price}</Typography>
                                    </Grid>
                                </Fragment>

                            ))
                        }


                        <Box width='100%' borderTop='2px solid rgba(0,0,0,0.6)' mt={2}>
                            <Box mt={3} display='flex' justifyContent='flex-end'>
                                <Stack>
                                    <Box display='flex' justifyContent='space-between'>
                                        <b><Typography variant='h7'>Total</Typography></b>
                                        <span><b>{subtotal}/-</b></span>
                                    </Box>
                                    <Button variant='contained' sx={{background:'#484848', marginTop: '7px' }} LinkComponent={Link} to='/products'>Shop Again</Button>
                                </Stack>
                            </Box>
                        </Box>


                    </Grid>

                </Grid>
            </Grid>

        </Container>
    )
}

export default OrderDetails;