import {createSlice} from '@reduxjs/toolkit';
import {toast} from 'react-toastify';

const initialState = {
    isAuthenticated: false,
    userName: "",
    userEmail:"",
    shippingDetails: localStorage.getItem("ShippingInfo") ? 
    JSON.parse(localStorage.getItem("ShippingInfo")):
    {},
    
}

const userSlice = createSlice({
    name:"user",
    initialState,
    reducers:{
        userLogedIn:(state, action)=>{
            localStorage.setItem("userInfo", JSON.stringify(action.payload));

            // const{name, email, login} = action.payload;
            // state.userName = name;
            // state.userEmail = email;
            // state.isAuthenticated = login;
            if(localStorage.getItem("userInfo") ){
                state.isAuthenticated = JSON.parse(localStorage.getItem("userInfo")).login;
                state.userName = JSON.parse(localStorage.getItem("userInfo")).name;
                state.userEmail = JSON.parse(localStorage.getItem("userInfo")).email;
                
            }
        },
        userLogOut:(state, action)=>{
           
            state.userName = "";
            state.userEmail = "";
            state.isAuthenticated = false;
            toast.success("Logout Successfull");
        },
        addShippingDetails:(state, action)=>{
            const{address,city,phone,pincode,country} = action.payload;
            state.shippingDetails = {
                address,
                city,
                phone,
                pincode,
                country
            }
        },
       

    }
});

export default userSlice.reducer;

export const {userLogedIn, userLogOut,addShippingDetails} = userSlice.actions;