import { Box, Button, Grid, Paper, Typography } from "@mui/material";
import React from "react";
import { useDispatch, useSelector } from "react-redux";
import { Link, useParams } from "react-router-dom";
import { addToCart } from "../store/slices/CartSlice";


const CategoryProducts=()=>{
    const dispatch = useDispatch();
    const params = useParams();
    console.log(params.category);
    let filterdata = useSelector((state) => state.filters.items);

    const categoryProd = filterdata.filter((item)=> item.fabric === params.category)
    console.log(categoryProd);
    const addItem = (item) => {
        dispatch(addToCart(item));
    }

    return(
        <Grid container spacing={2}>
                        {
                            categoryProd.length == 0 ? (
                                <h3>No Products Available</h3>
                            )
                                :
                                (
                                    categoryProd.map((data) => (

                                        <Grid item lg={4} md={6} sm={6} xs={12} textAlign="center" key={data._id}>
                                            <Paper elevation={2} component={Box} p={2}>
                                                <Button LinkComponent={Link} to={`/singleproduct/${data._id}`}>
                                                    <img src={data.url} width='100%' height="300px" style={{ objectFit: "contain" }} />
                                                </Button>
                                                <Typography>{data.title}</Typography>
                                                <Typography>{"("}{data.additional_info}{")"}</Typography>
                                                <Typography>Price:{data.price}/-</Typography>
                                                <Button variant='contained' onClick={() => addItem(data)}>Add to Cart</Button>
                                            </Paper>
                                        </Grid>
                                    ))
                                )
                        }
                    </Grid>
    )
}

export default CategoryProducts;