import { Button, Grid, TextField, Typography } from "@mui/material";
import { Container } from "@mui/system";
import { Link } from "react-router-dom";
import { toast } from "react-toastify";
const Profile=()=>{
    const changepassword=()=>{
        toast.error("Please contact admin for password updation");
    }
    var userInfo=JSON.parse(localStorage.getItem("userInfo"));
    return(
        <>
        <Container>
            <Grid container mt={10} mb={10} pt={10} pb={10} sx={{boxShadow:" 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19)",textAlign:"center"}}>
                <Grid item lg={12} md={12} sm={12} xs={12} mt={-5} pb={5}>
                    <h2>Profile</h2>
                </Grid>
                <Grid item lg={4} md={6} sm={6} xs={12}>
                    <Typography>Name</Typography><TextField
                            disabled
                            margin="normal"
                            label={`${userInfo.name}`}
                        />
                </Grid>
                <Grid item lg={4} md={6} sm={6} xs={12}>
                     <Typography>Email</Typography><TextField
                            disabled
                            margin="normal"
                            label={`${userInfo.email}`}
                        />
                </Grid>
                <Grid item lg={4} md={6} sm={6} xs={12}>
                     <Typography>Phone Number</Typography><TextField
                            disabled
                            margin="normal"
                            label={`${userInfo.phone}`}
                        />
                </Grid>
                <Grid item lg={4} md={6} sm={6} xs={12}>
                     <Typography>Age</Typography><TextField
                            disabled
                            margin="normal"
                            label={`${userInfo.age}`}
                            />
                </Grid>
                <Grid item lg={4} md={6} sm={6} xs={12}>
                     <Typography>Gender</Typography><TextField
                            disabled
                            margin="normal"
                            label={`${userInfo.gender}`}
                            />
                </Grid>
                <Grid item lg={4} md={6} sm={6} xs={12}>
                     <Typography>Address</Typography><TextField
                            disabled
                            margin="normal"
                            label={`${userInfo.address}`}
                        />
                </Grid>
                <Grid item lg={4} md={6} sm={6} xs={12}>
                     <Typography>Pincode</Typography><TextField
                            disabled
                            margin="normal"
                            label={`${userInfo.pincode}`}
                            />
                </Grid>
                <Grid item lg={12} md={12} sm={12} xs={12} mt={5}>
               <Button variant="contained" onClick={changepassword}>Change Password</Button>
                </Grid>
                <Grid item lg={12} md={12} sm={12} xs={12} mt={5}>
                    <Link style={{textDecoration:"none"}} to='/profileupdate'><Button variant="contained">Update Profile</Button></Link>
                </Grid>
            </Grid>
        </Container>
        </>
    )
}

export default Profile;