import { Grid, Box, Typography, Container, Button, Paper } from '@mui/material';
import React from 'react';
import { useSelector } from 'react-redux';
import { useNavigate } from 'react-router-dom';
import CheckoutSteps from '../Components/CheckoutSteps';

const Order = () => {
    const navigate = useNavigate();
    const cartProducts = useSelector((state) => state.carts);
    const {userName, userEmail, shippingDetails} = useSelector((state) => state.user);
    const {address, city,phone, country, pincode} = shippingDetails;
    const { cartItems, cartTotalAmount } = cartProducts;
    // console.log(cartItems , cartTotalAmount);
    const subtotal = cartItems.reduce((acc, item)=>acc + item.cartQuantity * item.price, 0);
    const shippingCharge = cartTotalAmount > 1000 ? 0 : 100;
    const tax = cartTotalAmount * 0.18;
    const total = cartTotalAmount + tax + shippingCharge;
    return (
        <Container sx={{ padding: { sm: '1rem 1rem', xs: '1rem 2rem' } }}>
            <CheckoutSteps activeStep={1} />
            <Grid container mt={2}>
                <Grid item xs={12} sm={7} md={8} lg={8}>
                    <Grid container spacing={4}>
                        <Grid item xs={10} sm={10} md={10} lg={10}>
                            <Box>
                                <Typography variant='h6' mt={2}>Shiping Info:</Typography> <hr/>
                                <Typography>Name: {userName}</Typography>
                                <Typography>Email: {userEmail}</Typography>
                                <Typography>Mob No: {phone}</Typography>
                                <Typography>Address: {address}, {city}, {country}-{pincode}</Typography>
                            </Box>
                        </Grid>
                        <Grid item xs={12} sm={11} md={11} lg={11} p={2}>

                            <Typography variant='h6'>Your Cart Items:</Typography> 
                            <Box mt={2}>
                            <hr/>
                                {
                                    cartItems.map((item) => (
                                        <Box key={item._id} display='flex' justifyContent='space-between' mb={2}>
                                            <img src={item.url} width="20%" />
                                            <Typography textAlign='end'>{item.title}</Typography>
                                            <Typography>{item.cartQuantity}  {item.price} = {item.price*item.cartQuantity}</Typography>
                                        </Box>
                                    ))
                                }
                            </Box>

                        </Grid>
                    </Grid>
                </Grid>
                <Grid item xs={12} sm={5} md={4} lg={4} mt={3}>
                    <Paper component={Box} p={2}>
                    <Typography variant='h6' textAlign='center'>Order Summary:</Typography>
                    <Box >
                        <hr/>
                        <Box display='flex' justifyContent='space-between' mt={3}>
                            <Typography>Subtotal:</Typography>
                            <span>{subtotal}</span>
                        </Box>
                        <Box display='flex' justifyContent='space-between'mt={1}>
                            <Typography>Shipping Charge:</Typography>
                            <span>{shippingCharge}</span>
                        </Box>
                        <Box display='flex' justifyContent='space-between'mt={1}>
                            <Typography>GST:</Typography>
                            <span>{tax}</span>
                        </Box>
                        <hr/>
                        <Box display='flex' justifyContent='space-between' mt={2}>
                            <Typography fontWeight='bold'>Total:</Typography>
                            <span><b> {total}</b></span>
                        </Box>
                        <Button variant='contained' fullWidth sx={{marginTop:'5px'}} onClick={()=>navigate('/order/payment')}>Proceed to Payment</Button>
                    </Box>
                    </Paper>
                </Grid>
            </Grid>
        </Container>
    )
}

export default Order;