import React, { useState } from 'react'
import { Avatar, Box, Button, Checkbox, Container, CssBaseline, FormControlLabel, Grid, TextField, Typography } from '@mui/material';
import LockOutlinedIcon from '@mui/icons-material/LockOutlined';
import { json, Link, useNavigate } from 'react-router-dom';
import { toast } from 'react-toastify';
import Loading from './Loading';

const Updatepassword = () => {
  const [loading,setLoading]=useState(false);
  const [password,setPassword]=useState(null)
  const [confirmpassword,setConfirmpassword]=useState(null)
  const email=JSON.parse(localStorage.getItem("Info")).email;
  const data={email,password};
const navigate=useNavigate();
const handleSubmit=(e)=>{
  e.preventDefault();
  setLoading(true);
  if(password==confirmpassword){
    fetch("http://193.160.119.184:9236/updatePassword",{ 
      method:'put', 
      headers:{ 
        'Accept':'Application/json', 
        'Content-Type':'Application/json' 
      }, 
      body:JSON.stringify(data)
    }).then((result)=>{ 
      result.json().then((result2)=>{ 
        if(result2.status==201){ 
          setLoading(false);
          toast.success(result2.message) 
          navigate('/login');
        }else{
          setLoading(false);
          toast.warn(result2.message);
        }
         }) 
        }) 
      }else{
        setLoading(false);
        toast.error("Password not matched !")
  }
}

  return (
    <>
    {loading?<Loading/>:
    
    <Container component="main" maxWidth="xs" sx={{paddingBottom:"2rem",boxShadow:"rgb(32 32 32 / 8%) 0px 10px 32px 0px;"}}>
        <CssBaseline />
        <Box
          sx={{
            marginTop: 5,
            display: 'flex',
            flexDirection: 'column',
            alignItems: 'center',
          }}
        >
          <Avatar sx={{ m: 1, bgcolor: 'secondary.main' }}>
            <LockOutlinedIcon />
          </Avatar>
          <Typography component="h1" variant="h5">
            Update Your Password
          </Typography>
          <Box component="form" noValidate onSubmit={handleSubmit} sx={{ mt: 3 }}>
            <Grid container spacing={2}>
              <Grid item xs={12} sm={12} md={12} lg={12} xl={12}>
                <TextField
                  disabled
                  label={JSON.parse(localStorage.getItem("Info")).email}
                  fullWidth
                />
            </Grid>
            <Grid item xs={12} sm={12} md={12} lg={12} xl={12}>
                <TextField
                  label="Password"
                  hidden
                  fullWidth
                  onChange={(e)=>setPassword(e.target.value)}
                />
            </Grid>
            <Grid item xs={12} sm={12} md={12} lg={12} xl={12}>
                <TextField
                  label="Confirm Password"
                  fullWidth
                  onChange={(e)=>setConfirmpassword(e.target.value)}
                />
            </Grid>
            <Grid item xs={12} sm={12} md={12} lg={12} xl={12}>
            <Button
              type="submit"
              fullWidth
              variant="contained"
              sx={{ mt: 3, mb: 2 }}
            >
              Update
            </Button>
            </Grid>
          </Grid>
          </Box>
        </Box>
      </Container>
    }
  </>
  )
}

export default Updatepassword;
