import React, { useState } from 'react'
import { Avatar, Box, Button, Checkbox, Container, CssBaseline, FormControlLabel, Grid, TextField, Typography } from '@mui/material';
import LockOutlinedIcon from '@mui/icons-material/LockOutlined';
import { Link, useNavigate } from 'react-router-dom';
import { toast } from 'react-toastify';
import Loading from './Loading';
const Verify = () => {
  const [loading,setLoading]=useState(false);
  const navigate=useNavigate();
  const email=JSON.parse(localStorage.getItem("Info")).email;
  const data={email};
  const code=JSON.parse(localStorage.getItem("Info")).OTP;
  const [otp, setOTP]=useState(null);
  function handleSubmit(e){
    e.preventDefault();
    setLoading(true);
    if(code == otp){
      fetch("http://193.160.119.184:9236/verify",{ 
        method:'put', 
        headers:{ 
          'Accept':'Application/json', 
          'Content-Type':'Application/json' 
        }, 
        body:JSON.stringify(data)
      }).then((result)=>{ 
        result.json().then((result2)=>{ 
          if(result2.status==201){ 
            setLoading(false);
            toast.success(result2.message) 
            navigate('/login');
          }else{
          setLoading(false);
          toast.warn(result2.message);
        }
      }) 
    }) 
    navigate('/login');
  }
  else{
        setLoading(false);
        toast.error("invalid otp");
    }
    
}

  return (
    <>{loading?<Loading/>:
    
    <Container component="main" maxWidth="xs" sx={{paddingBottom:"2rem",boxShadow:"rgb(32 32 32 / 8%) 0px 10px 32px 0px;"}}>
        <CssBaseline />
        <Box
          sx={{
            marginTop: 5,
            display: 'flex',
            flexDirection: 'column',
            alignItems: 'center',
          }}
        >
          <Avatar sx={{ m: 1, bgcolor: 'secondary.main' }}>
            <LockOutlinedIcon />
          </Avatar>
          <Typography component="h1" variant="h5">
            Sign up
          </Typography>
          <Box component="form" noValidate onSubmit={handleSubmit} sx={{ mt: 3 }}>
            <Grid container spacing={2}>
              <Grid item xs={12} sm={12} md={12} lg={12} xl={12}>
                <TextField
                  autoComplete="given-name"
                  name="firstName"
                  required
                  fullWidth
                  id="firstName"
                  label="Enter Otp"
                  autoFocus
                  onChange={(e)=>setOTP(e.target.value)}
                />
            </Grid>
            <Grid item xs={12} sm={12} md={12} lg={12} xl={12}>
            <Button
              type="submit"
              fullWidth
              variant="contained"
              sx={{ mt: 3, mb: 2 }}
            >
              Validate Otp
            </Button>
            </Grid>
          </Grid>
          </Box>
        </Box>
      </Container>
    }
  </>
  )
}

export default Verify;
