import { Button, Grid, TextField, Typography } from "@mui/material";
import { Container } from "@mui/system";
import axios from "axios";
import { useState } from "react";
import { json, Link } from "react-router-dom";
import { toast } from "react-toastify";
import Loading from "./Loading";
const ProfileUpdate = () => {
    const [loading, setLoading] = useState(false);
    var userInfo = JSON.parse(localStorage.getItem("userInfo"));
    const email = userInfo.email;
    const [phone, setPhone] = useState(null);
    const [gender, setGender] = useState(null);
    const [age, setAge] = useState(null);
    const [address, setAddress] = useState(null);
    const [pincode, setPincode] = useState(null);
    const data = { email, phone, gender, age, address, pincode };
    const handleSubmit = (e) => {
        e.preventDefault();
        setLoading(true);
        fetch('http://193.160.119.184:9236/profile', {
            method: 'put',
            headers: {
                'Accept': 'Application/json',
                'Content-Type': 'Application/json'
            },
            body: JSON.stringify(data)
        }).then((result) => {
            result.json().then((result2) => {
                if (result2.status == 200) {
                    setLoading(false);
                    localStorage.clear();
                    toast.success(result2.message);
                    localStorage.setItem("userInfo", JSON.stringify(result2.userInfo));
                } else {
                    setLoading(false);
                    toast.error(result2.message);
                }
            })
        })

    }
    return (
        <>
            {loading ? <Loading /> :

                <Container>
                    <form onSubmit={handleSubmit}>
                        <Grid container mt={10} mb={10} pt={10} pb={10} sx={{ boxShadow: " 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19)", textAlign: "center" }}>
                            <Grid item lg={12} md={12} sm={12} xs={12} mt={-5} pb={5}>
                                <h2>Profile</h2>
                            </Grid>
                            <Grid item lg={4} md={6} sm={6} xs={12}>
                                <Typography>Name</Typography><TextField
                                    disabled
                                    margin="normal"
                                    label={`${userInfo.name}`}
                                />
                            </Grid>
                            <Grid item lg={4} md={6} sm={6} xs={12}>
                                <Typography>Email</Typography><TextField
                                    disabled
                                    margin="normal"
                                    label={`${userInfo.email}`}
                                />
                            </Grid>
                            <Grid item lg={4} md={6} sm={6} xs={12}>
                                <Typography>Phone Number</Typography><TextField
                                    margin="normal"
                                    type="text"
                                    pattern="[0-9]{10}"
                                    required
                                    onChange={(e) => { setPhone(e.target.value) }}
                                    label="Phone Number"
                                />
                            </Grid>
                            <Grid item lg={4} md={6} sm={6} xs={12}>
                                <Typography>Age</Typography><TextField
                                    margin="normal"
                                    required
                                    label="Age"
                                    onChange={(e) => { setAge(e.target.value) }}
                                />
                            </Grid>
                            <Grid item lg={4} md={6} sm={6} xs={12}>
                                <Typography>Gender</Typography><TextField
                                    margin="normal"
                                    required
                                    onChange={(e) => { setGender(e.target.value) }}
                                    label="Gender"
                                />
                            </Grid>
                            <Grid item lg={4} md={6} sm={6} xs={12}>
                                <Typography>Address</Typography><TextField
                                    margin="normal"
                                    required
                                    onChange={(e) => { setAddress(e.target.value) }}
                                    label="Address"
                                />
                            </Grid>
                            <Grid item lg={4} md={6} sm={6} xs={12}>
                                <Typography>Pincode</Typography><TextField
                                    margin="normal"
                                    required
                                    label="Pincode"
                                    onChange={(e) => { setPincode(e.target.value) }}
                                />
                            </Grid>
                            <Grid item lg={12} md={12} sm={12} xs={12} mt={5}>
                                <Button type='submit' variant="contained">Update</Button>
                            </Grid>
                            <Grid item lg={12} md={12} sm={12} xs={12} mt={5} p={5}>
                                <i>if you want to update your email or name just sent us email</i>
                            </Grid>
                        </Grid>
                    </form>
                </Container>
            }
        </>
    )
}

export default ProfileUpdate;