import { BrowserRouter, Routes, Route} from 'react-router-dom';
import './App.css';
import Navbar from './Components/Navbar';
import Home from './Pages/Home';
import Product from './Pages/Product';
import Cart from './Pages/Cart';
import Login from './Pages/Login';
import Signup from './Pages/Signup';
import Blog from './Pages/Blog';
import About from './Pages/About';
import Verify from './Pages/Verify';
import Profile from './Pages/Profile';
import ProfileUpdate from './Pages/ProfileUpdate';
import SingleBlog from './Pages/SingleBlog';
import Footer from './Pages/Footer';
import SingleProduct from './Pages/SingleProduct';
import { ToastContainer } from 'react-toastify';
import "react-toastify/dist/ReactToastify.css"
import VerifyLogin from './Pages/VerifyLogin';
import VerifyOTP from './Pages/VerifyOTP';
import Forgot from './Pages/Forgot';
import Updatepassword from './Pages/Updatepassword';
import Resetaccountverify from './Pages/Resetaccountverify';
import { useEffect } from 'react';
import Shipping from './Pages/Shipping';
import Order from './Pages/Order';
import { userLogedIn } from './store/slices/UserSlice';
// import { productsFetch } from './store/slices/FilterSlice';
import { useDispatch } from 'react-redux';
import Payment from './Pages/Payment';
import Succeess from './Pages/Success';
import OrderDetails from './Pages/OrderDetails';
import CategoryProducts from './Pages/CategoryProducts';

function App() {

  const dispatch = useDispatch();
  // useEffect(()=>{
  //   dispatch(productsFetch());
  // },[])

  useEffect(()=>{
    if(localStorage.getItem("userInfo")){
     dispatch(userLogedIn(JSON.parse(localStorage.getItem("userInfo"))));
    }
 },[])

  return (
    <div className="App">
       <BrowserRouter>
       <ToastContainer/>
       <Navbar/>
       {/* <Header/> */}
       <Routes>
        <Route path='/' element={<Home/>}/>
        <Route path='/products' element={<Product/>}/>
        <Route path='/products/:category' element={<CategoryProducts/>}/>
        <Route path='/about' element={<About/>}/>
        <Route path='/blog' element={<Blog/>}/>
        <Route path='/blog/blog/:id' element={<SingleBlog/>}/>
        <Route path='/cart' element={<Cart/>}/>
        <Route path='/login' element={<Login/>}/>
        <Route path='/signup' element={<Signup/>}/>
        <Route path='/singleproduct/:id' element={<SingleProduct/>}/>
        <Route path='/verify' element={<Verify/>}/>
        <Route path='/profile' element={<Profile/>}/>
        <Route path='/profileupdate' element={<ProfileUpdate/>}/>
        <Route path='/verifylogin' element={<VerifyLogin/>}/>
        <Route path='/verifyotp' element={<VerifyOTP/>}/>
        <Route path='/resetaccount' element={<Forgot/>}/>
        <Route path='/resetaccountverify' element={<Resetaccountverify/>}/>
        <Route path='/updatepassword' element={<Updatepassword/>}/>
        <Route path='/shipping' element={<Shipping/>}/>
        <Route path='/order' element={<Order/>}/>
        <Route path='/order/payment' element={<Payment/>}/>
        <Route path='/order/success' element={<Succeess/>}/>
        <Route path='/orderdetails' element={<OrderDetails/>}/>
       </Routes>
       <Footer/>
       </BrowserRouter>
    </div>
  );
}

export default App;
