import React , {useState} from 'react'
import { Drawer, List, ListItemButton, Button, IconButton, ListItemIcon, Badge, Box, Menu,MenuItem, Tooltip, Avatar, Typography } from '@mui/material';
import MenuIcon from '@mui/icons-material/Menu';
import ShoppingCartIcon from '@mui/icons-material/ShoppingCart';
import { Link, useNavigate } from 'react-router-dom';
import { useDispatch, useSelector } from 'react-redux';
import { userLogOut } from '../store/slices/UserSlice';

const DrawerComp = () => {
    const[openDrawer, setOpenDrawer] = useState(false);
    const Pages = ["Home", "Products", "Blog", "About"];
    const settings = ['Profile', 'Orders', 'Logout'];
    const[open, setOpen] = useState(false);
    const navigate = useNavigate();
    const dispatch = useDispatch();
    const cartItem = useSelector((state)=> state.carts.cartItems);
    const {isAuthenticated, userName} = useSelector((state)=> state.user);
    const logOut = (setting)=>{
      
         if(setting == "Logout"){
          dispatch(userLogOut())
         localStorage.clear();
         navigate('/');
         }
         if(setting=="Profile"){
          navigate('/profile');
         }
        //  if(setting=="Orders"){
        //    navigate('/orderdetails');
        //  }
   }
  return (
    <>
      <Drawer open={openDrawer} onClose={()=>setOpenDrawer(false)}>
          <List>
          {
            Pages.map((page, index)=>(
              <ListItemButton key={index} onClick={()=>setOpenDrawer(false)} sx={{padding:'10px 20px', marginBottom:"10px"}}>
                <ListItemIcon>
                   {/* <ListItemText sx={{width:300}} component={Link} to={`/${page}`}>{page}</ListItemText> */}
                   
                   <Link to={index > 0 ? `/${page}` : '/'} style={{ textDecoration:"none", color:"inherit", width:"200px"}}>{page}</Link>
                  
                </ListItemIcon>
              </ListItemButton>
            ))
          }
          <ListItemButton onClick={()=>setOpenDrawer(false)} sx={{padding:'10px 20px', marginBottom:"10px"}}>
            <ListItemIcon>
            {
              !isAuthenticated && 
               <Link to='/login' style={{ textDecoration:"none", color:"inherit", width:"150px"}}>Login</Link>
            }
            </ListItemIcon>
          </ListItemButton>
          </List>
        </Drawer>

        <Button LinkComponent={Link} to='/cart'>
              <Badge badgeContent={cartItem.length} color="primary">
                <ShoppingCartIcon color='inherit'/>
              </Badge>
        </Button>
        {
          isAuthenticated &&  
          <Box sx={{ flexGrow: 0.02 }}>
               <Tooltip title={userName}>
                 <IconButton  sx={{ p: 0 }} onClick={()=>setOpen(!open)}>
                   <Avatar alt={userName} src="/static/images/avatar/2.jpg" />
                 </IconButton>
               </Tooltip>
               <Menu
                 sx={{ mt: '45px' }}
                 id="menu-appbar"
                 anchorOrigin={{
                   vertical: 'top',
                   horizontal: 'right',
                 }}
                 keepMounted
                 transformOrigin={{
                   vertical: 'top',
                   horizontal: 'right',
                 }}
                 open={open}
                 onClose={(e)=>setOpen(false)}
               >
                 {settings.map((setting) => (
                   <MenuItem key={setting}>
                     <Typography textAlign="center" onClick={()=> logOut(setting)}>{setting}</Typography>
                   </MenuItem>
                 ))}
               </Menu>
             </Box>
        }
        <IconButton onClick={()=>setOpenDrawer(!openDrawer)}>
            <MenuIcon/>
        </IconButton>
    </>
  )
}

export default DrawerComp;

