import React, { useEffect, useState } from 'react'
import { Avatar, Box, Button, Container, CssBaseline, Grid, IconButton, InputAdornment, TextField, Typography } from '@mui/material';
import LockOutlinedIcon from '@mui/icons-material/LockOutlined';
import VisibilityIcon from '@mui/icons-material/Visibility';
import VisibilityOffIcon from '@mui/icons-material/VisibilityOff';
import AccountCircleIcon from '@mui/icons-material/AccountCircle';
import { Link, useNavigate , useLocation} from 'react-router-dom';
import {toast} from 'react-toastify'
import { useDispatch } from 'react-redux';
import { userLogedIn } from '../store/slices/UserSlice';
import Loading from './Loading';

const Login = () => {
  const [loading,setLoading]=useState(false);
  const navigate=useNavigate();
  const location = useLocation();
  const dispatch = useDispatch();
  const[eye, setEye] = useState(false);
  const [email,setEmail]=useState(null);
  const [password,setPassword]=useState(null);
  const data={email,password};
  const redirect = location.search ? location.search.split('=')[1] : '/';
  
  const handleEye=()=>{
    setEye(!eye);
  }
  const handleSubmit=(e)=>{ 
    e.preventDefault(); 
    setLoading(true);
    if(email && password) 
    { 
      fetch("http://193.160.119.184:9236/login",{ 
        method:'post', 
            headers:{ 
              'Accept':'Application/json', 
              'Content-Type':'Application/json' 
            }, 
            body:JSON.stringify(data) 
          }).then((result)=>{ 
           result.json().then((result2)=>{ 
             if(result2.status==501){ 
               setLoading(false);
               toast.warn(result2.message);
               navigate('/verifylogin');
              } 
              else if(result2.status==503){ 
                setLoading(false);
                toast.error(result2.message) 
                document.getElementById("form").reset(); 
              }else if(result2.status==201){
                setLoading(false);
                dispatch(userLogedIn(result2.userInfo))
                // localStorage.setItem("userInfo", JSON.stringify(result2.userInfo));
              // console.log(JSON.parse(localStorage.getItem("userInfo")).name);
              navigate(redirect);
              toast.success(`Welcome ${result2.userInfo.name}`);
              
            }else{
              setLoading(false);
              toast.info(result2.message);
              document.getElementById("form").reset(); 
            }
          }) 
        }) 
      
    }else{ 
        setLoading(false);
      toast.warn('Please input all required filled') 
    } 
}

  return (
    <>
    {loading?<Loading/>:
    <Container component="main" maxWidth="xs" sx={{paddingBottom:"2.5rem",boxShadow:"rgb(32 32 32 / 8%) 0px 10px 32px 0px;",marginBottom:'2rem'}}>
    <CssBaseline />
    <Box
      sx={{
        marginTop: 5,
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',
      }}
    >
      <Avatar sx={{ m: 1, bgcolor: 'secondary.main' }}>
        <LockOutlinedIcon />
      </Avatar>
      <Typography component="h1" variant="h5">
        Sign in
      </Typography>
      <Box component="form" onSubmit={handleSubmit} noValidate sx={{ mt: 1 }}>
        <TextField
          margin="normal"
          required
          fullWidth
          id="email"
          label="Email Address"
          name="email"
          autoComplete="email"
          autoFocus
          onChange={(e)=>setEmail(e.target.value)}
          InputProps={{
            endAdornment: (
              <InputAdornment position='start'>
                <AccountCircleIcon color='primary'/>
              </InputAdornment>
            )
          }}
        />
        <TextField
          margin="normal"
          required
          fullWidth
          name="password"
          label="Password"
          type={eye? "text" : "password"}
          id="password"
          autoComplete="current-password"
          onChange={(e)=>setPassword(e.target.value)}
          InputProps={{
            endAdornment: (
              <InputAdornment position='end'>
                <IconButton onClick={handleEye}>
                 {eye ? <VisibilityIcon color='primary'/> : <VisibilityOffIcon color='primary'/>}
                </IconButton>
              </InputAdornment>
            )
          }}
        />
        <Button
          type="submit"
          fullWidth
          variant="contained"
          sx={{ mt: 3, mb: 2 }}
        >
          Sign In
        </Button>
        <Grid container>
          <Grid item xs>
            <Link to={('/resetaccount')}>Forgot password?</Link>
          </Grid>
          <Grid item>
            Don't have an account?{" "}
            <Link to="/signup" variant="body2" sx={{color:"rgb(241, 99, 52)"}}>
              {"Sign Up"}
            </Link>
          </Grid>
        </Grid>
      </Box>
    </Box>
  </Container>

}
</>
      )
}

export default Login;
