import { Box, Button, Slider, FormControlLabel, FormControl, FormLabel, Grid, Radio, RadioGroup, Typography, useTheme, useMediaQuery, Paper } from '@mui/material';
import React, { useEffect, useState } from 'react';
import FilterDrawerComp from './FilterDrawerComp';
import { useDispatch, useSelector } from 'react-redux';
import { addToCart } from '../store/slices/CartSlice';
import { Link } from 'react-router-dom';
import { sortByCategory, sortByPrice, sortByRange, getMaxAndMin } from '../store/slices/FilterSlice';


const Product = () => {

    const dispatch = useDispatch();
    let filterdata = useSelector((state) => state.filters.items);
    // let isLoading = useSelector((state)=> state.filters.isLoading);
    const maxPrice = useSelector((state) => state.filters.maxPrice);
    const minPrice = useSelector((state) => state.filters.minPrice);
    const theme = useTheme();
    const isMatch = useMediaQuery(theme.breakpoints.down('md'));
    // const Category = ["Cotton", "Cotton + Irri Silk", "Marino Wool", "Pashmina Wool"];
    const filterByCategory = (event) => {
        const value = event.target.defaultValue;
        dispatch(sortByCategory(value))
    }

    const addItem = (item) => {
        dispatch(addToCart(item));
    }

    const filterByPrice = (event) => {
        const value = event.target.defaultValue;
        dispatch(sortByPrice(value));
    }

    const filterByRange = (event) => {
        const value = event.target.value;
        dispatch(sortByRange(value));
    }

    useEffect(() => {
        dispatch(getMaxAndMin());
    }, [filterdata])

    // if(isLoading){
    //     return(<h3 style={{textAlign:'center'}}>Loading....</h3>)
    //  }
    return (
        <>
            <Grid container columnSpacing={2}>
                <Grid item lg={3}>
                    {
                        isMatch ? (
                            <FilterDrawerComp />
                        ) :
                            (
                                <Box p={2}>
                                    <Typography textAlign="center" fontSize={17} variant='h6'>Filter</Typography>
                                    <Box>
                                        <FormControl>
                                            <FormLabel >Category</FormLabel>
                                            <RadioGroup
                                                defaultValue="All"
                                                name="radio-buttons-group"
                                            >
                                                <FormControlLabel value="All" control={<Radio />} label="All" onChange={(e) => filterByCategory(e)} />
                                                <FormControlLabel value="Men" control={<Radio />} label="Men" onChange={(e) => filterByCategory(e)} />
                                                <FormControlLabel value="Women" control={<Radio />} label="Women" onChange={(e) => filterByCategory(e)} />
                                            </RadioGroup>
                                        </FormControl>
                                    </Box>
                                    <Box mt={2}>
                                        <FormControl>
                                            <FormLabel >Sort By Price</FormLabel>
                                            <RadioGroup
                                                defaultValue="All"
                                                name="radio-buttons-group"
                                            >
                                                <FormControlLabel value="LTH" control={<Radio />} label="Low to High" onChange={(e) => filterByPrice(e)} />
                                                <FormControlLabel value="HTL" control={<Radio />} label="High to Low" onChange={(e) => filterByPrice(e)} />
                                            </RadioGroup>
                                        </FormControl>
                                    </Box>
                                    <Box sx={{ width: 250 }} mt={2}>
                                        <FormLabel >Sort By Range</FormLabel>
                                        <Slider
                                            aria-label="Restricted values"
                                            defaultValue={200}
                                            valueLabelDisplay="auto"
                                            min={minPrice}
                                            max={maxPrice}
                                            onChange={(e) => filterByRange(e)}
                                        />
                                        <Button variant='contained' sx={{ marginTop: '10px' }}>Clear Filter</Button>
                                    </Box>

                                </Box>
                            )
                    }
                </Grid>
                <Grid item lg={9} p={2}>
                
                    <Grid container spacing={2}>
                        {
                            filterdata.length == 0 ? (
                                <h3>No Products Available</h3>
                            )
                                :
                                (
                                    filterdata.map((data) => (

                                        <Grid item lg={4} md={6} sm={6} xs={12} textAlign="center" key={data._id}>
                                            <Paper elevation={2} component={Box} p={2}>
                                                <Button LinkComponent={Link} to={`/singleproduct/${data._id}`}>
                                                    <img src={data.url} width='100%' height="300px" style={{ objectFit: "contain" }} />
                                                </Button>
                                                <Typography>{data.title}</Typography>
                                                {/* <Typography>{"("}{data.additional_info}{")"}</Typography> */}
                                                <Typography>Price:{data.price}/-</Typography>
                                                <Button variant='contained' onClick={() => addItem(data)}>Add to Cart</Button>
                                            </Paper>
                                        </Grid>
                                    ))
                                )
                        }
                    </Grid>
                </Grid>
            </Grid>
        </>
    )
}

export default Product;

