import React, { Fragment } from 'react'
import { Link, useNavigate } from 'react-router-dom'
import ShoppingBagIcon from '@mui/icons-material/ShoppingBag';
import { Box, Button, Grid, Paper, Stack, Typography, useMediaQuery, useTheme } from '@mui/material'
import { useDispatch, useSelector } from 'react-redux'
import { clearCart, decreaseQuantity, deleteItem, increaseQuantity } from '../store/slices/CartSlice'

const Cart = ({ history }) => {
    const navigate = useNavigate();
    const dispatch = useDispatch();
    const theme = useTheme();
    const isMatch = useMediaQuery(theme.breakpoints.down('sm'));
    const title = ["PRODUCT", "PRICE", "QUANTITY", "TOTAL"];
    const cartItem = useSelector((state) => state.carts.cartItems);
    const { isAuthenticated } = useSelector((state) => state.user);
    const totalAmount = useSelector((state) => state.carts.cartTotalAmount);
    const removeItem = (id) => {
        dispatch(deleteItem(id));
    }
    const incQuantity = (id) => {
        dispatch(increaseQuantity(id));
    }
    const decQuantity = (id) => {
        dispatch(decreaseQuantity(id));
    }

    const checkoutHandler = (e) => {
        if (isAuthenticated) {
            navigate('/shipping');
        }
        else {
            navigate('/login?redirect=/shipping');
        }

    }
    return (
        <>
            <Typography textAlign="center" variant='h6' mt={2}>Shopping Cart</Typography>

            {
                isMatch ? (

                    cartItem.length == 0 ? (
                        <Box textAlign="center" mt={4} p={5}>
                            <ShoppingBagIcon sx={{ fontSize: '8rem' }} color='info' />
                            <Typography variant="h6">Your Cart is Empty!</Typography>
                            <Typography>Add a few products and come here to checkout.</Typography>
                        </Box>
                    ) : (
                        <>
                            <Grid container p={2} >
                                {
                                    cartItem.map((item) => (
                                        <Paper component={Box} mb={3} key={item._id} elevation={4}>
                                            <Grid item sm={12} p={2}>
                                                <img src={item.url} width='100%' style={{ objectFit: "contain" }} />
                                            </Grid>
                                            <Grid item sm={12} p={2}>
                                                <Box >
                                                    <Box display='flex' alignItems='center' gap={1}>
                                                        <Typography>{item.title}</Typography>
                                                        <small>{"("}{item.additional_info}{")"}</small>
                                                    </Box>
                                                    <Typography mt={1}>Price : Rs {item.price}/-</Typography>
                                                </Box>
                                                <Box mt={2}>
                                                    <Button variant='outlined' sx={{ minWidth: 50, padding: 1, marginRight: "10px" }} onClick={() => incQuantity(item._id)}>+</Button>
                                                    {item.cartQuantity}
                                                    <Button variant='outlined' sx={{ minWidth: 50, padding: 1, marginLeft: "10px" }} onClick={() => decQuantity(item._id)}>-</Button>
                                                </Box>
                                                <Typography mt={2}>Total: Rs {item.cartQuantity * item.price}/-</Typography>
                                            </Grid>
                                        </Paper>
                                    ))
                                }
                            </Grid>
                            <Box p={2} display='flex' justifyContent='space-between' flexDirection='column'>
                                <Box>
                                    <Button variant='contained' onClick={() => dispatch(clearCart())}>Clear Cart</Button>
                                </Box>
                                <Paper component={Box} mt={3} p={3} elevation={4}>
                                    <Stack>
                                        <Box display='flex' justifyContent='space-between'>
                                            <Typography variant='h7' fontWeight='bold'>SubTotal</Typography>
                                            <Typography variant='h7' fontWeight='bold'>Rs {totalAmount}/-</Typography>
                                        </Box>
                                        <Typography mt={1}>Taxes and Shipping Calculated at Checkout</Typography>
                                        <Button variant='contained' sx={{ marginTop: '10px' }} onClick={checkoutHandler}>Checkout</Button>
                                        <Button variant='contained' sx={{ marginTop: '10px' }} LinkComponent={Link} to='/products'>Continue Shopping</Button>
                                    </Stack>
                                </Paper>
                            </Box>
                        </>
                    )
                ) :
                    (
                        cartItem.length == 0 ? (
                            <Box textAlign="center" mt={2} p={4} >
                                <ShoppingBagIcon sx={{ fontSize: '6rem' }} color='info' />
                                <Typography variant="h6">Your Cart is Empty!</Typography>
                                <Typography>Add a few products and come here to checkout.</Typography>
                            </Box>
                        ) :
                            (
                                <>
                                    <Grid container mt={5} textAlign="center" >
                                        {
                                            title.map((value, index) => (
                                                <Grid item lg={3} md={3} sm={3} key={index}>
                                                    {value}
                                                </Grid>
                                            ))
                                        }
                                    </Grid>
                                    <hr />
                                    <Grid container mt={5} textAlign="center">
                                        {
                                            cartItem.map((item) => (
                                                <Fragment key={item._id}>
                                                    <Grid item lg={3} md={3} sm={3} p={4} borderBottom="1px solid rgb(0,0,0.6)">
                                                        <img src={item.url} width='100%' />
                                                        <Typography mt={2}>{item.title}</Typography>
                                                        <span>{"("}{item.additional_info}{")"}</span>
                                                    </Grid>

                                                    <Grid item lg={3} md={3} sm={3} p={4} borderBottom="1px solid rgb(0,0,0.6)">
                                                        <Typography mb={2}>Rs {item.price}/-</Typography>
                                                        <Button variant='contained' onClick={() => removeItem(item._id)}>Remove</Button>
                                                    </Grid>

                                                    <Grid item lg={3} md={3} sm={3} p={4} borderBottom="1px solid rgb(0,0,0.6)">
                                                        <Box display='flex' gap={2} alignItems='center' justifyContent='center'>
                                                            <Button variant='outlined' sx={{ minWidth: 50, padding: 1 }} onClick={() => incQuantity(item._id)}>+</Button>
                                                            {item.cartQuantity}
                                                            <Button variant='outlined' sx={{ minWidth: 50, padding: 1 }} onClick={() => decQuantity(item._id)}>-</Button>
                                                        </Box>
                                                    </Grid>

                                                    <Grid item lg={3} md={3} sm={3} p={4} borderBottom="1px solid rgb(0,0,0.6)" >
                                                        <Typography>Rs {item.cartQuantity * item.price}/-</Typography>
                                                    </Grid>
                                                </Fragment>
                                            ))
                                        }
                                    </Grid>
                                    <Box p={2} display='flex' justifyContent='space-between'>
                                        <Box>
                                            <Button variant='contained' onClick={() => dispatch(clearCart())}>Clear Cart</Button>
                                        </Box>
                                        <Paper component={Box}  p={2} elevation={4}>
                                            <Stack>
                                                <Box display='flex' justifyContent='space-between'>
                                                    <Typography variant='h6'>SubTotal</Typography>
                                                    <Typography variant='h6'>Rs {totalAmount}/-</Typography>
                                                </Box>
                                                <Typography paragraph>Taxes and Shipping Calculated at Checkout</Typography>
                                                <Button variant='contained' onClick={checkoutHandler}>Checkout</Button>
                                                <Button variant='contained' sx={{ marginTop: '7px' }} LinkComponent={Link} to='/products'>Continue Shopping</Button>
                                            </Stack>
                                        </Paper>
                                    </Box>
                                </>
                            )
                    )
            }

        </>
    )
}

export default Cart;

