import React, { useState } from 'react'
import { Box, Button, Container, Grid, InputAdornment, TextField, Typography } from '@mui/material';
import HomeIcon from '@mui/icons-material/Home';
import LocationCityIcon from '@mui/icons-material/LocationCity';
import PinDropIcon from '@mui/icons-material/PinDrop';
import PublicIcon from '@mui/icons-material/Public';
import SmartphoneIcon from '@mui/icons-material/Smartphone';
import { Link, useNavigate } from 'react-router-dom';
import { toast } from 'react-toastify'
import { useDispatch } from 'react-redux';
import { addShippingDetails, userLogedIn } from '../store/slices/UserSlice';
import CheckoutSteps from '../Components/CheckoutSteps';
import { useForm } from 'react-hook-form';

const Shipping = () => {
  const navigate = useNavigate();
  const dispatch = useDispatch();
  const{register, handleSubmit, formState: {errors}} = useForm({
    mode:'onTouched',
    defaultValues: {
      address:'',
      city:'',
      phone:'',
      pincode:'',
      country:'India'
    }
  });

  const onSubmit = (data) => {
    localStorage.setItem("ShippingInfo", JSON.stringify(data));
    console.log(data);
    dispatch(addShippingDetails(data));
    navigate('/order');
  }

  return (

      <Container component="main" maxWidth="sm" sx={{ boxShadow: "rgb(32 32 32 / 8%) 0px 10px 32px 0px;" }}>
        <Box
          sx={{
            marginTop: 5,
            marginBottom: 5,
            paddingTop: 5,
            display: 'flex',
            flexDirection: 'column',
            textAlign: 'center'

          }}
        >
          <CheckoutSteps activeStep={0} />
          <Typography component="h1" variant="h5" mt={3}>
            Shipping Details
          </Typography>
          <Box component="form" onSubmit={handleSubmit(onSubmit)} sx={{ mt: 1 }}>

            <TextField
              margin="normal"
              
              fullWidth
              label="Address"
              id="outlined-error-helper-text"
              name="address"
              autoComplete="address"
              autoFocus
              InputProps={{
                startAdornment: (
                  <InputAdornment position='start'>
                    <HomeIcon color='primary' />
                  </InputAdornment>
                )
              }}
              {
                ...register("address", {required:"Address is required."})
              }
              error={Boolean(errors.address)}
              helperText={errors.address?.message}
            />

            <TextField
              margin="normal"
              
              fullWidth
              id="city"
              label="City"
              name="city"
              autoComplete="city"
              InputProps={{
                startAdornment: (
                  <InputAdornment position='start'>
                    <LocationCityIcon color='primary' />
                  </InputAdornment>
                )
              }}
              {
                ...register("city", {required:"City is required."})
              }
              error={Boolean(errors.city)}
              helperText={errors.city?.message}
            />

            <TextField
              margin="normal"
              
              fullWidth
              id="phone"
              label="Phone No"
              name="phone"
              autoComplete="phone"
              InputProps={{
                startAdornment: (
                  <InputAdornment position='start'>
                    <SmartphoneIcon color='primary' />
                  </InputAdornment>
                )
              }}
              {
                ...register("phone", {required:"Phone Number is required.", pattern:{
                  value:/^\d{10}$/,
                  message:"Please enter a valid 10 digits Phone Number."
                }})
              }
              error={Boolean(errors.phone)}
              helperText={errors.phone?.message}
            />

            <TextField
              margin="normal"
             
              fullWidth
              id="pincode"
              label="Pincode"
              name="pincode"
              autoComplete="pincode"
              InputProps={{
                startAdornment: (
                  <InputAdornment position='start'>
                    <PinDropIcon color='primary' />
                  </InputAdornment>
                )
              }}
              {
                ...register("pincode", {required:"Pincode is required.", minLength:{
                  value:6,
                  message:"PinCode must be 6 digits."
                }})
              }
              error={Boolean(errors.pincode)}
              helperText={errors.pincode?.message}
            />

            <TextField
              margin="normal"
              required
              fullWidth
              id="country"
              label="Country"
              name="country"
              defaultValue='India'
              autoComplete="pincode"
              disabled
              autoFocus
              InputProps={{
                startAdornment: (
                  <InputAdornment position='start'>
                    <PublicIcon color='primary' />
                  </InputAdornment>
                )
              }}
              
            />

            <Typography color='primary'>Products will be deliver to the above provided address.</Typography>
            <Button
              type="submit"
              variant="contained"
              sx={{ mt: 3, mb: 2 }}
            >
              Continue
            </Button>
          </Box>
        </Box>
      </Container>
    
  )
}

export default Shipping;
