import React from 'react'
import { Typography ,Box, Grid, Card, Button,CardHeader, CardMedia, CardContent, CardActions, CardActionArea,  Container, Paper} from '@mui/material'
import content from '../blogContent'
import { Link, useParams } from 'react-router-dom';

const SingleBlog = () => {
   const {id} = useParams();
   const blog = content.filter((data)=> data.id == id);
    return (
      // <Container>
      //     <img src={blog[0].image} width='100%' sx={{objectFit:'cover', height:{xs:'50vh', sm:'60vh'}}}/>
      //     <Paper component={Box} p={2} sx={{marginTop:'-5rem'}}>
      //        <Typography variant='h5'>{blog[0].title}</Typography>
      //        <Typography variant='h7'>{blog[0].desc}</Typography>
      //     </Paper>

      //  </Container>
       <Card>
                <CardMedia
                component="img"
                height="500"
                width="200"
                image={blog[0].image}
                alt="green iguana"
                />
                <CardContent sx={{padding:'1rem'}}>
                <Typography gutterBottom variant="h5" component="div">
                  {blog[0].title}
                </Typography>
                <Typography  variant="h6" color="info">
                {blog[0].fulldesc}
                </Typography>
                </CardContent>
            
        </Card> 
    )
  }
  
  export default SingleBlog;