import React, { useEffect } from 'react'
import { Box, Button, Container, Grid, Paper, Stack, Typography } from '@mui/material'
import banner1 from '../images/Banner_1.png'
import AutoModeIcon from '@mui/icons-material/AutoMode';
import VolunteerActivismIcon from '@mui/icons-material/VolunteerActivism';
import LocalShippingIcon from '@mui/icons-material/LocalShipping';
import SecurityIcon from '@mui/icons-material/Security';
import { ImQuotesLeft } from 'react-icons/im'
import { useSelector } from 'react-redux'
import { Link } from 'react-router-dom'

const category = [
  {
    id: 1,
    image: 'https://media.istockphoto.com/id/472111673/photo/pashmina-shawls-scarves-displayed-at-an-outdoor-market.jpg?s=612x612&w=0&k=20&c=Jf7mGHheCndKemQCKMnrnAuyzEWGuglY5im744hJI24=',
    title: "Cotton"
  },
  {
    id: 2,
    image: 'https://media.istockphoto.com/id/522556574/photo/cotton-crops.jpg?s=612x612&w=0&k=20&c=CVQtfK9GLX2poic0oyn4apegCK9UtAIS20rmEPtIdhk=',
    title: "Cotton + Irri Silk"
  },
  {
    id: 3,
    image: 'https://media.istockphoto.com/id/1402298576/photo/pile-of-persian-and-other-rugs-for-sale-in-the-shop-specializing-in-home-furnishings.jpg?s=612x612&w=0&k=20&c=hF5_-Ey3i7wwuwlWigXvSMFoC6zNznsiEh0Puwx5Vms=',
    title: "Marino Wool"
  },
  {
    id: 4,
    image: 'https://media.istockphoto.com/id/92459279/photo/scarf.jpg?s=612x612&w=0&k=20&c=VtSoGUQJedwCI_CTwndqz5cAESjqLmJIqalflR0bZXo=',
    title: "Pashmina Wool"
  }
]
const Home = () => {
  const product = useSelector((state) => state.filters.items);
  const trendingProd = product.filter((item) => item.trending == true);

  return (
    <>
      <Box component='div' mb={2}>
        <Box component='img' sx={{ objectFit: 'contain', width: '100%', height: '100%' }} src={banner1}></Box>
      </Box>


      <Box display='flex' justifyContent='space-around' p={2} mb={2} gap={2} minWidth='16rem'>

        <Box display='flex' flexDirection='column'>
          <AutoModeIcon sx={{ fontSize: { sm: '4rem', xs: '2rem' } }} color='inherit' />
          <span style={{ marginTop: "5px" }}>EASY EXCHANGE</span>
        </Box>
        <Box display='flex' flexDirection='column'>
          <VolunteerActivismIcon sx={{ fontSize: { sm: '4rem', xs: '2rem' } }} color='inherit' />
          <span style={{ marginTop: "5px" }}>100% HANDPICKED</span>
        </Box>
        <Box display='flex' flexDirection='column'>
          <LocalShippingIcon sx={{ fontSize: { sm: '4rem', xs: '2rem' } }} color='inherit' />
          <span style={{ marginTop: "5px" }}>FAST DELIVERY</span>
        </Box>
        <Box display='flex' flexDirection='column'>
          <SecurityIcon sx={{ fontSize: { sm: '4rem', xs: '2rem' } }} color='inherit' />
          <span style={{ marginTop: "5px" }}>SECURE PAYMENT</span>
        </Box>
      </Box>

      <Box mt={10}>
        <Typography variant='h4' textAlign='center' fontWeight={500} >Trending Products</Typography>
        <Grid container p={2} mt={2} minWidth='16rem'>
          {
            trendingProd.map((item) => (
              <Grid item sm={6} md={4} lg={4} key={item._id} textAlign='center' p={2}>
                <Link to={`/singleproduct/${item._id}`}>
                  <img src={item.url} style={{ width: '300px' }} />
                </Link>
                <Typography textAlign='center' mt={2}>{item.title}</Typography>
                <Typography textAlign='center'>{"("}{item.additional_info}{")"}</Typography>
              </Grid>
            ))
          }
        </Grid>
      </Box>

      <Container>
        <Typography variant='h4' textAlign='center' mb={3}>Category</Typography>
        <Grid container spacing={2}>
          {
            category.map((categ) => (
              <Grid item xs={12} sm={12} md={12} lg={6} key={categ.id}>
                <Paper component='Box' elevation={4} sx={{
                  backgroundImage: `url(${categ.image})`,
                  backgroundSize: 'cover',
                  backgroundPosition: 'center',
                  height: 450, display: 'flex', justifyContent: 'center', alignItems: 'center',
                  '&:hover': {
                    backgroundColor: '#e5e7eb',
                    cursor: 'pointer'
                  }
                }}
                >
                  <Stack>
                  <Typography color='white' variant='h4' letterSpacing={2} mb={2}>{categ.title}</Typography>
                  <Button variant='contained' LinkComponent={Link} to={`/products/${categ.title}`}>View Products</Button>
                  </Stack>
                </Paper>
              </Grid>
            ))
          }

        </Grid>
      </Container>

      <Box m="8rem 0rem 6.66667rem 0rem" position='relative' pl="5%" pr='5%' minWidth="16rem">
        <ImQuotesLeft style={{ color: 'rgb(242, 116, 106)', position: 'absolute', top: '5px', left: '6%', transform: 'scale(1.5)', transformOrigin: 'bottom' }} />
        {/* <Typography color='#153a5b' fontSize='1.5rem'>We see our customers as invited guests to a party, and we are the hosts. Its our job every day to make every important aspect of the customer experience a little bit better.</Typography> */}
        <Typography color='#153a5b' fontWeight={550} sx={{ fontSize: { xs: '1.5rem', sm: '2rem' }, paddingLeft: { xs: '2rem', sm: "2.5rem" } }}>Choose local, weave a better community - support your local weavers today!</Typography>
      </Box>
    </>
  )
}

export default Home;

