import { Box } from '@mui/material';
import React from 'react'
import LocalShippingIcon from '@mui/icons-material/LocalShipping';
import DeliveryDiningIcon from '@mui/icons-material/DeliveryDining';
import CurrencyRupeeIcon from '@mui/icons-material/CurrencyRupee';
import ReplayIcon from '@mui/icons-material/Replay';

function Features() {
  return (
    <Box display='flex' justifyContent='space-between' mt={2} sx={{width:{sm:'60%', md:'70%'}}}>
       <Box display='flex' flexDirection='column'>
         <DeliveryDiningIcon color='primary' sx={{fontSize:{sm:'2rem', md:'2.5rem'}}}/>
         <small>Fast</small>
         <small>Delivery</small>
       </Box>
       <Box display='flex' flexDirection='column'>
         <ReplayIcon color='primary' sx={{fontSize:{sm:'2rem', md:'2.5rem'}}}/>
         <small>3 days</small>
         <small>return policy</small>
       </Box>
       <Box display='flex' flexDirection='column'>
         <CurrencyRupeeIcon color='primary' sx={{fontSize:{sm:'2rem', md:'2.5rem'}}}/>
         <small>Cash on</small>
         <small>Delivery</small>
       </Box>
       <Box display='flex' flexDirection='column'>
         <LocalShippingIcon color='primary' sx={{fontSize:{sm:'2rem', md:'2.5rem'}}}/>
         <small>Swarn Sutra </small>
         <small>Delivery</small>
       </Box>
    </Box>
  )
}

export default Features;